.container {
  font-family: Arial, sans-serif;
  padding: 20px;
}

.card {
  border-radius: 8px;
  overflow: hidden;
  
  .card-header {
    padding: 1rem;
  }
}

.form-group {
  margin-bottom: 1rem;
  
  label {
    font-weight: 500;
    margin-bottom: 0.5rem;
  }
}

.input-group-text {
  background-color: #f8f9fa;
  font-weight: 500;
}

